<template>
  <div>
    <h1>{{id?'编辑':'新建'}}物品</h1>
    <el-form label-width="120px" @submit.native.prevent="submit">
      <el-form-item label="名称">
        <el-input v-model="model.name"></el-input>
      </el-form-item>
      <el-form-item label="图标">
        <el-upload
          class="avatar-uploader"
          :action="uploadUrl"
          :headers="getAuthHeaders()"
          :show-file-list="false"
          :on-success="res=>$set(model,'icon',res.result.url)"
        >
          <img v-if="model.icon" :src="model.icon" class="avatar" />
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit">保存</el-button>
      </el-form-item>
    </el-form>
    {{$http.axios}}
  </div>
</template>
<script>
export default {
  props: {
    // :action="$http.defaults.baseURL+ '/upload'"
    // 通过路由设置 props: true 接口id
    id: {}
  },
  data() {
    return {
      model: {
      }
    }
  },
  mounted() {
    console.log(1)
    // this.id存在才执行this.getDataById(this.id)
    this.id && this.getDataById(this.id)
  },
  methods: {
    async submit() {
      let res;
      if (this.id) {
        res = await this.$http(this, { method: 'put', url: `rest/items/${this.id}`, data: this.model })
      } else {
        res = await this.$http(this, { method: 'post', url: 'rest/items', data: this.model })
      }
      this.$message({
        type: 'success',
        message: res.msg
      });
      this.$router.push('/items/list')
    },
    async getDataById(id) {
      const res = await this.$http(this, { method: 'get', url: `rest/items/${id}` });
      this.model = res.result
    },
    afterUpload(res) {
      console.log(res);
      // this.model.icon = res.url会赋值不上
      // 使用显式赋值
      this.$set(this.model, 'icon', res.url)
    }
  }
}
</script>

<style>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>
