<template>
  <div>
    <el-card header="请先登录" class="login-card">
      <el-form @submit.native.prevent="login">
        <el-form-item label="用户名">
          <el-input type="text" v-model="model.username"></el-input>
        </el-form-item>
        <el-form-item label="密码">
          <el-input type="password" v-model="model.password"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" native-type="submit">登录</el-button>
        </el-form-item>
      </el-form>
    </el-card>
  </div>
</template>
<script>
export default {
  data() {
    return {
      model: {}
    }
  },
  methods: {
    async login() {
      const res = await this.$http(this, { url: 'login', method: 'post', data: this.model });
      // const res = await this.$axios.post('login', this.model)
      this.$message({
        type: 'success',
        message: res.msg
      });
      localStorage.token = res.result
      this.$router.push('/ads/list')
    }

  }
}
</script>
<style scoped>
.login-card {
  width: 25rem;
  margin: 5rem auto;
}
</style>