<template>
  <div>
    <h1>{{id?'编辑':'新建'}}分类</h1>
    <el-form label-width="120px" @submit.native.prevent="submit">
      <el-form-item label="上级分类">
        <el-select v-model="model.parent">
          <el-option v-for="i in parents" :key="i._id" :value="i._id" :label="i.name">{{i.name}}</el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="名称">
        <el-input v-model="model.name"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  props: {
    // 通过路由设置 props: true 接口id
    id: {}
  },
  data() {
    return {
      model: {},
      parents: []
    }
  },
  mounted() {
    // this.id存在才执行this.getDataById(this.id)
    this.id && this.getDataById(this.id)
    this.fetchParents()
  },
  methods: {
    async submit() {
      let res;
      if (this.id) {
        res = await this.$http(this, { method: 'put', url: `rest/categories/${this.id}`, data: this.model })
      } else {
        res = await this.$http(this, { method: 'post', url: 'rest/categories', data: this.model })
      }
      this.$message({
        type: 'success',
        message: res.msg
      });
      this.$router.push('/categories/list')
    },
    async getDataById(id) {
      const res = await this.$http(this, { method: 'get', url: `rest/categories/${id}` });
      this.model = res.result;
    },
    async fetchParents() {
      const res = await this.$http(this, { url: 'rest/categories', method: 'get' })
      this.parents = res.result;
    },
  }
}
</script>