<template>
  <div>
    <el-table :data="tableData" style="width: 100%">
      <el-table-column prop="_id" label="id"></el-table-column>
      <el-table-column prop="name" label="英雄名称"></el-table-column>
      <el-table-column prop="icon" label="头像">
        <template slot-scope="scope">
          <img :src="scope.row.avatar" style="height:3rem" alt />
        </template>
      </el-table-column>
      <el-table-column label="操作" width="100">
        <template slot-scope="scope">
          <el-button
            @click="$router.push(`/heroes/edit/${scope.row._id}`)"
            type="text"
            size="small"
          >编辑</el-button>
          <el-button @click="handleDelete(scope.row)" type="text" size="small">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>
<script>
export default {
  data() {
    return {
      tableData: []
    }
  },
  created() {
    this.getData()
  },
  methods: {
    async getData() {
      const res = await this.$http(this, { url: 'rest/heroes', method: 'get' })
      this.tableData = res.result
    },
    handleDelete(row) {
      this.$confirm(`是否要删除${row.name}`, "提示", {
        confirmButtonText: "确定",
        cancleButtonText: "取消",
        type: "warning"
      }).then(async () => {
        const res = await this.$http(this, {
          url: `rest/heroes/${row._id}`,
          method: 'delete'
        })
        this.$message({
          type: 'success',
          message: res.msg
        });
        this.getData()
      })
    }
  }
}
</script>