<template>
  <div>
    <h1>{{id?'编辑':'新建'}}文章</h1>
    <el-form label-width="120px" @submit.native.prevent="submit">
      <el-form-item label="所属分类">
        <el-select v-model="model.categories" multiple>
          <el-option v-for="i in categories" :key="i._id" :value="i._id" :label="i.name">{{i.name}}</el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="标题">
        <el-input v-model="model.title"></el-input>
      </el-form-item>
      <el-form-item label="详情">
        <vue-editor v-model="model.body" useCustomImageHandler @image-added="handleImageAdded"></vue-editor>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
import { VueEditor } from 'vue2-editor'
export default {
  props: {
    // :action="$http.defaults.baseURL+ '/upload'"
    // 通过路由设置 props: true 接口id
    id: {}
  },
  components: {
    VueEditor
  },
  data() {
    return {
      categories: [],
      model: {
      }
    }
  },
  mounted() {
    this.fetchCategories()
    // this.id存在才执行this.getDataById(this.id)
    this.id && this.getDataById(this.id)
  },
  methods: {
    async submit() {
      let res;
      if (this.id) {
        res = await this.$http(this, { method: 'put', url: `rest/articles/${this.id}`, data: this.model })
      } else {
        res = await this.$http(this, { method: 'post', url: 'rest/articles', data: this.model })
      }
      this.$message({
        type: 'success',
        message: res.msg
      });
      this.$router.push('/articles/list')
    },
    async getDataById(id) {
      const res = await this.$http(this, { method: 'get', url: `rest/articles/${id}` });
      this.model = res.result
    },
    async fetchCategories() {
      const res = await this.$http(this, { method: 'get', url: `rest/categories` });
      this.categories = res.result;
    },
    async handleImageAdded(file, Editor, cursorLocation, resetUploader) {
      const formData = new FormData()
      formData.append('file', file)
      const res = await this.$http(this, { method: 'post', url: `upload`, data: formData });
      Editor.insertEmbed(cursorLocation, 'image', res.result.url)
      resetUploader()
    }
  }
}
</script>

<style>
.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
</style>
