<template>
  <div>
    <h1>{{id?'编辑':'新建'}}广告位</h1>
    <el-form label-width="120px" @submit.native.prevent="submit">
      <el-form-item label="名称">
        <el-input v-model="model.name"></el-input>
      </el-form-item>
      <el-form-item label="广告">
        <el-button size="small" @click="model.items.push({})">
          <i class="el-icon-plus"></i> 添加广告
        </el-button>
        <el-row type="flex" style="flex-wrap:wrap">
          <el-col :md="24" v-for="(item,k) in model.items" :key="k">
            <el-form-item label="链接">
              <el-input v-model="item.url"></el-input>
            </el-form-item>
            <el-form-item label="图片">
              <el-upload
                class="avatar-uploader"
                :action="uploadUrl"
                :headers="getAuthHeaders()"
                :show-file-list="false"
                :on-success="res=>$set(item,'image',res.result.url)"
              >
                <img v-if="item.image" :src="item.image" class="avatar" />
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item>
              <el-button size="small" type="danger" @click="model.items.splice(k,1)">删除</el-button>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  props: {
    // 通过路由设置 props: true 接口id
    id: {}
  },
  data() {
    return {
      model: {
        items: []
      },
      token: ''
    }
  },
  mounted() {
    // this.id存在才执行this.getDataById(this.id)
    this.id && this.getDataById(this.id);
  },
  methods: {
    async submit() {
      let res;
      if (this.id) {
        res = await this.$http(this, { method: 'put', url: `rest/ads/${this.id}`, data: this.model })
      } else {
        res = await this.$http(this, { method: 'post', url: 'rest/ads', data: this.model })
      }
      this.$message({
        type: 'success',
        message: res.msg
      });
      this.$router.push('/ads/list')
    },
    async getDataById(id) {
      const res = await this.$http(this, { method: 'get', url: `rest/ads/${id}` });
      this.model = Object.assign({}, this.model, res.result)
    }
  }
}
</script>