<template>
  <div>
    <h1>{{id?'编辑':'新建'}}管理员</h1>
    <el-form label-width="120px" @submit.native.prevent="submit">
      <el-form-item label="名称">
        <el-input v-model="model.username"></el-input>
      </el-form-item>
      <el-form-item label="密码">
        <el-input type="text" v-model="model.password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" native-type="submit">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  props: {
    // 通过路由设置 props: true 接口id
    id: {}
  },
  data() {
    return {
      model: {},
      parents: []
    }
  },
  mounted() {
    this.id && this.getDataById(this.id)
  },
  methods: {
    async submit() {
      let res;
      if (this.id) {
        res = await this.$http(this, { method: 'put', url: `rest/admin_users/${this.id}`, data: this.model })
      } else {
        res = await this.$http(this, { method: 'post', url: 'rest/admin_users', data: this.model })
      }
      this.$message({
        type: 'success',
        message: res.msg
      });
      this.$router.push('/admin_users/list')
    },
    async getDataById(id) {
      const res = await this.$http(this, { method: 'get', url: `rest/admin_users/${id}` });
      this.model = res.result
    }
  }
}
</script>