import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Login from './views/Login.vue'

import CreateCategories from './views/CreateCategories.vue'
import ListCategories from './views/ListCategories.vue'

import CreateItems from './views/CreateItems'
import ListItems from './views/ListItems'

import CreateHero from './views/CreateHero'
import ListHero from './views/ListHero'

import CreateArticle from './views/CreateArticle'
import ListArticle from './views/ListArticle'

import CreateAds from './views/CreateAds.vue'
import ListAds from './views/ListAds.vue'

import CreateAdminUsers from './views/CreateAdminUsers.vue'
import ListAdminUsers from './views/ListAdminUsers.vue'

Vue.use(Router)

const router = new Router({
  routes: [{
    path: '/login',
    name: 'login',
    component: Login,
    // 设置一个标志 代表该页面为是否公开页面，若其他页面无isPublic则代表不是公开访问页面 需要token才可以访问
    meta: {
      isPublic: true
    }
  }, {
    path: '/',
    name: 'home',
    component: Home,
    children: [{
        path: '/categories/create',
        component: CreateCategories
      }, {
        path: '/categories/edit/:id',
        component: CreateCategories,
        // 将id传入props
        props: true
      },
      {
        path: '/categories/list',
        component: ListCategories
      }, {
        path: '/items/create',
        component: CreateItems
      }, {
        path: '/items/edit/:id',
        component: CreateItems,
        // 将id传入props
        props: true
      }, {
        path: '/items/list',
        component: ListItems
      }, {
        path: '/heroes/create',
        component: CreateHero
      }, {
        path: '/heroes/edit/:id',
        component: CreateHero,
        // 将id传入props
        props: true
      }, {
        path: '/heroes/list',
        component: ListHero
      }, {
        path: '/articles/create',
        component: CreateArticle
      }, {
        path: '/articles/edit/:id',
        component: CreateArticle,
        // 将id传入props
        props: true
      }, {
        path: '/articles/list',
        component: ListArticle
      }, {
        path: '/ads/create',
        component: CreateAds
      }, {
        path: '/ads/edit/:id',
        component: CreateAds,
        // 将id传入props
        props: true
      }, {
        path: '/ads/list',
        component: ListAds
      }, {
        path: '/admin_users/create',
        component: CreateAdminUsers
      }, {
        path: '/admin_users/edit/:id',
        component: CreateAdminUsers,
        // 将id传入props
        props: true
      }, {
        path: '/admin_users/list',
        component: ListAdminUsers
      }
    ]
  }]
})
router.beforeEach((to, from, next) => {
  // 如果进入的页面没有公开页面标志且无localStorage.token则跳转去login页面
  if (!to.meta.isPublic && !localStorage.token) {
    return next('/login')
  }
  next();
})

export default router